// import react from "react";
import Input from "../Input";

const List = ({user}) => {
  return (
    <div className="header list">
      <Input id="list-add-bar" type="text">
        Adicionar
      </Input>
      <Input
        classname="list-add-button"
        type="button"
        icon="akar-icons:plus"
      ></Input>
    </div>
  );
};

export default List;
